import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/user',
    name: 'User',
    component: () => import(/* webpackChunkName: "about" */ '../views/User.vue')
  },
  {
    path: '/res',
    name: 'Responsibility',
    component: () => import(/* webpackChunkName: "about" */ '../views/Res.vue')
  },
  {
    path: '/plan',
    name: 'Plan',
    component: () => import(/* webpackChunkName: "about" */ '../views/Plan.vue')
  },
  {
    path: '/eplan',
    name: 'EMBPlanList',
    component: () => import(/* webpackChunkName: "about" */ '../views/EPlan.vue')
  },
  {
    path: '/land',
    name: 'LandClass',
    component: () => import(/* webpackChunkName: "about" */ '../views/LClass.vue')
  },
  {
    path: '/nature',
    name: 'NatureBusiness',
    component: () => import(/* webpackChunkName: "about" */ '../views/NBusiness.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
