import con from './config'

const state = {
       landclass: [],
       currentRowNo: 0
}
     
const getters = {
       allLand: state => state.landclass,
       lgetCurrentrow: state => state.currentRowNo,
}

const actions = {
      
    async fetchLandList({ commit }, row) {
        await con.get(`/Land/${row.limit}/${row.offset}`).then(function (res) {
          if (res.data['Land'].length > 0) {
            // commit data until last
            commit('getLand', res.data['Land']);
          } else {
            // minus 10 if last data meet
            var rowoffset = row.offset - row.limit;
            if(rowoffset<=0){
              rowoffset = 0;
            }
            commit('currentRow', rowoffset);
          }
    
        }).catch(error => {
          if(error.response.data.error=="Expired token"){
            sessionStorage.clear();
            window.location.href="https://iis.emb.gov.ph/embis/";
          }
       })
    },
    async increment({ commit }, row) {
        var limitoffset = parseInt(row.offset) + parseInt(row.limit);
        commit('currentRow', limitoffset);
    },
    async decrement({ commit }, row) {
        // decrement
        var limitoffset = parseInt(row.offset) - parseInt(row.limit);
        commit('currentRow', limitoffset);
        if (row.offset <= 0) {
            // commit to 0 if data is less than zero
            commit('currentRow', 0);
        }
    },
    async searchLandList({ commit }, res) {
        var limit = parseInt(res.limit);
        await con.post('/SearchLandList', {
          txtsearch: res.txtsearch,
          txtlimit: limit
        }).then(function (res) {
          commit('currentRow', res.data['offset']);
          commit('getLand', res.data['LandList']);
        }).catch(error => {
          if(error.response.data.error=="Expired token"){
            sessionStorage.clear();
            window.location.href="https://iis.emb.gov.ph/embis/";
          }
       })
    },
    async CreateLandClass({ commit }, res) {
        await con.post(`/CreateLandClass/${res.limit}/${res.offset}`,
          {
            class: res.class
          }).then(function (res) {
            commit('getLand', res.data['Land']);
            swal({
                title: "Land Classification",
                text: "Save Successfully",
                icon: "success",
                button: true,     
              });
          }).catch(error => {
            if(error.response.data.error=="Expired token"){
              sessionStorage.clear();
              window.location.href="https://iis.emb.gov.ph/embis/";
            }else{
              var errorLog = JSON.stringify(error.response.data);
              swal({
                title: "Server Side Validation",
                text: errorLog,
                icon: "error",
                dangerMode: true,     
              });
            }
         }) 
    },
    async UpdateLandClass({ commit }, val) {
        const res = await con.put(`/UpdateLandClass/${val.id}`, {
          class: val.class
        }).catch(error => {
          if(error.response.data.error=="Expired token"){
            sessionStorage.clear();
            window.location.href="https://iis.emb.gov.ph/embis/";
          }
       })
        commit('getLand', res.data);
    },

    async deleteLandList({ commit }, res) {
        const result = await con.delete(`/DeleteLandClass/${res.id}/${res.limit}/${res.offset}`).catch(error => {
          if(error.response.data.error=="Expired token"){
            sessionStorage.clear();
            window.location.href="https://iis.emb.gov.ph/embis/";
          }
       })
      commit('getLand', result.data['Res']);
    },

       
};

const mutations = {
       getLand: (state, Land) => (state.landclass = Land),
       currentRow: (state, Irow) => (state.currentRowNo = Irow),
}

export default {
       state,
       getters,
       actions,
       mutations
};